      <p>睿课学是一款由福建睿和科技有限公司（以下简称“我们”）研发的教育APP产品。我们将通过以下《隐私政策》帮助您了解睿课学会如何收集、使用和存储您的个人信息及您享有何种权利。要点如下：
      </p>
      <h3> 隐私政策</h3>
      <p>
        睿课学尊重并保护所有使用睿课学服务用户的个人隐私权。为了给您提供更准确、更有个性化的服务，教育APP会按照本隐私权政策的规定使用和披露您的个人信息。但睿课学将以高度审慎态度对待这些信息。除本隐私权政策另有规定外，在未征得您事先许可的情况下，睿课学不会将这些信息对外披露或向第三方提供。睿课学会定期更新本隐私权政策。
      </p>
      <p> 您在同意睿课学网络服务使用协议之时，即视为您已经同意本隐私权政策全部内容。本隐私权政策属于睿课学网络服务使用协议不可分割的一部分。 </p>
      <h3>1. 适用范围</h3>
      <p> a)
          在您使用睿课学时，睿课学将自动接收并记录您的浏览器和计算机上的信息，包括但不限于您的IP地址、浏览器的类型、使用的语言、访问日期和时间、软硬件特征信息及您需求的网页记录等数据；
      </p>
