      <p> 您了解并同意，以下信息不适用本隐私权政策： </p>
      <p>&nbsp;&nbsp; a) 您在使用睿课学提供的搜索服务时输入的关键字信息； </p>
      <p>&nbsp;&nbsp; b) 睿课学讲收集到您在睿课学发布的相关信息数据，包括但不限于参与活动、互动、课堂记录等； </p>
      <p>&nbsp;&nbsp; c) 违反法律规定或违反睿课学规则行为及睿课学已对您采取的措施。 </p>
