      </p>
      <p> b) 睿课学亦不允许任何第三方以任何手段收集、编辑、出售或者无偿传播您的个人信息。任何睿课学用户如从事上述活动，一经发现，睿课学有权立即终止与该用户的服务协议。 </p>
      <p> c)
