      <p> 在如下情况下，睿课学将依据您的个人意愿或法律的规定全部或部分的披露您的个人信息： </p>
      <p>&nbsp;&nbsp; a) 经您事先同意，向第三方披露； </p>
      <p>&nbsp;&nbsp; b) 为提供您所要求的产品和服务，而必须和第三方分享您的个人信息； </p>
      <p>&nbsp;&nbsp; c) 根据法律的有关规定，或者行政或司法机构的要求，向第三方或者行政、司法机构披露； </p>
