      <p> 睿课学收集的有关您的信息和资料将保存在睿课学及（或）其关联公司的服务器上，这些信息和资料可能传送至您所在国家、地区或睿课学收集信息和资料所在地的境外并在境外被访问、存储和展示。
      </p>
