      <h3>6. 信息安全</h3>
      <p>
        睿课学帐号均有安全保护功能，请妥善保管您的用户名及密码信息。睿课学将通过对用户密码进行加密等安全措施确保您的信息不丢失，不被滥用和变造。尽管有前述安全措施，但同时也请您注意在信息网络上不存在“完善的安全措施”。
      </p>

